// CONTACTS INFO (ALT) SECTION

.contact {
    &_header {
        .text {
            margin: 30px 0;
        }
    }
    &_info {
        margin-bottom: 30px;
    }
    .wrapper {
        &--bg {
            margin-top: 50px;
            background-color: #F8F8F8;
            padding: 30px;
        }
    }
}

// ------ MEDIA QUERIES

@media screen and (min-width: 767.98px) {
    .contact {
        &_info {
            margin-bottom: 40px;
        }
    }
}

@media screen and (min-width: 991.98px) {
    .contact {
        &_header {
            .text {
                margin: 20px 0 30px;
            }
        }
        .wrapper {
            &--bg {
                margin-top: 0;
                padding: 60px;
            }
        }
    }
}